import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text } from '@chakra-ui/react';
import axios from 'axios';


import {apiURL,Units} from "../../modules/interfaces"

import Header from "../../components/Header";
import BackButton from "../../components/buttons/BackButton";
import BatteryCard from "../../components/cards/BatteryCard";
import SearchBar from "../../components/SearchBar";

const UnitsViewPage = () => {
    //const { location } = props;

    const defaultUnits:Units[] = [];
    const [units, setUnits]: [Units[], (units: Units[]) => void] = React.useState(defaultUnits);

    //battryで一斉取得
    //->battery_id取得
    React.useEffect(() => {
    axios
        .get<Units[]>(apiURL+"api/v1/units/", {
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            setUnits(response.data);
        });
    }, []);
    console.log('units data should be fetched at this point');
    console.log(units)

    //それぞれのbattery_idからunitID&customerIDを取得
    //unitIDからunitデータを取得（エラー出ているのみを取得？）＋customerID

    const history = useHistory();

    return (
        <div>
        <Header
          drawerItems={[
            {
              title: "ホーム",
              path: "/",
            },
            {
              title: "バッテリー",
              path: "/units",
            },
            {
              title: "お客様",
              path: "/customer-view",
            },
          ]}
          //iconName={iconname}
        />
        <Center flexDirection="column">
            <Text
                w="290px"
                fontSize="22px"
                fontWeight="bold"
                margin="10px"
            >
                バッテリー稼働状況
            </Text>
            {/*
            <Box>
                <SearchBar
                    onChange={()=>{
                        //TODO:フィルタリングの処理
                    }}
                />
                </Box>*/}
            {units.map((value) => {
                return (
                    <Box margin="15px">
                        <BatteryCard
                            remainingAmount={value.soc}
                            unitID={value.unit_id}
                            batteryType={value.profile.unit_type}
                            company={value.customer_name}
                            purpose={value.profile.purpose}
                            requiredAction={value.required_action}
                            onClick={() => {
                                history.push({
                                    pathname: `/unit-detail`,
                                    state: {
                                        contractID: value.contract_id,
                                        unitID: value.unit_id
                                        /*
                                        unitData: {
                                            remainingAmount: value.soc,
                                            unitID: value.unit_id,
                                            batteryType: value.profile.unit_type,
                                            company: value.customer_name,
                                            purpose: value.profile.purpose,
                                        }
                                        */
                                    }
                                });
                            }}
                        />
                    </Box>
                );
            })}
            <Box marginBottom="10px">
                <BackButton
                    onClick= {() => {
                        history.goBack();
                    }}
                />
            </Box>
        </Center>
        </div>
    );
}

export default UnitsViewPage;