import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text, Divider } from '@chakra-ui/react';
import axios from 'axios';

import {apiURL,Units,Customers} from "../../modules/interfaces"

import Header from "../../components/Header";
import SearchBar from "../../components/SearchBar";
import AddCustomerButton from "../../components/buttons/AddButton";
import CompanyCard from "../../components/cards/CompanyCard";

const CustomerViewPage = () => {

    const defaultCustomers:Customers[] = [];
    const [customers, setCustomers]: [Customers[], (customers: Customers[]) => void] = React.useState(defaultCustomers);

    React.useEffect(() => {
        axios
            .get<Customers[]>(apiURL+"api/v1/customers/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setCustomers(response.data);
            });
        }, []);
        console.log('customers data should be fetched at this point');
        console.log(customers)

    const history = useHistory();

    return (
        <div>
        <Header
          drawerItems={[
            {
              title: "ホーム",
              path: "/",
            },
            {
              title: "バッテリー",
              path: "/units",
            },
            {
              title: "お客様",
              path: "/customer-view",
            },
          ]}
          //iconName={iconname}
        />
        <Center flexDirection="column">
            <Text
                w="290px"
                fontSize="22px"
                fontWeight="bold"
                margin="10px"
            >
                お客様一覧
            </Text>
            {/*
            <Box>
                <Text fontSize="14px" margin="10px">新規契約登録：お客様詳細ページから、事業所を選択</Text>
            </Box>
            */}
            <Divider/>
            {/*
            <Box>
                <SearchBar
                    onChange={() => {
                        //TODO:フィルタリングの処理を入れる
                    }}
                />
            </Box>
            <Box margin="30px">
                <AddCustomerButton
                    label="顧客を追加"
                    onClick={() => {
                        history.push(`/registration-view`);
                    }}
                />
            </Box>
            */}
            {customers.map((value) => {
                return (
                    <Box marginTop="30px">
                        <CompanyCard
                            customerID={String(value.account_id)}
                            companyName={value.corporation_name}
                            jobType={value.sector}
                            onClick={() => {
                                history.push({
                                    //pathname: "/customer-units",
                                    pathname: "/customer-info",
                                    state: {
                                        account_id: value.account_id,
                                    }
                                });
                            }}
                        />
                    </Box>
                );
            })}
        </Center>
        </div>
    );
}

export default CustomerViewPage;