import React, {useState} from 'react';
import { useHistory } from 'react-router-dom';
import {
    Center,
    Text,
    Button,
    Flex,
    Box,
} from '@chakra-ui/react';

import AddButton from "../../components/buttons/AddButton";

import CarMiniCard from "../../components/cards/CarMiniCard";
import ChargerMiniCard from "../../components/cards/ChargerMiniCard";
import { connect } from 'node:http2';

interface BatteryRegistrationViewPageProps {
    location: {
        state:{
            company: string,
            officeName: string,
            contractName: string,
            contract_id: number,
            account_id: number,
            department_id: number,
            carName?: string,
            battery_option_id?: number [],
            chargerName?: string,
            charger_id?: number [],
            charger_name?: string[],
            option_name?: string[],
        }
    }
}

const BatteryRegistrationViewPage = (props: BatteryRegistrationViewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const contractName = location.state.contractName;
    const contract_id = location.state.contract_id;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    const battery_option_id = location.state.battery_option_id || [];
    const charger_id = location.state.charger_id || [];
    const option_name = location.state.option_name || [];
    const charger_name = location.state.charger_name || [];
    //const [carNameState, setCarNameState] = useState(location.state.carName || "");
    //const [chargerNameState, setChargerNameState] = useState(location.state.chargerName || "test");

    const [update, setUpdate] = useState<number>(0);
    let cnt:number = 0
    //const update_key = "update"
    //console.log(update)

    //const [update2, setUpdate2] = useState<string>("");
    //const update_key2 = "update2"
    //console.log(update2)

    const history = useHistory();

    console.log("battery_option_id & charger_id")
    console.log(battery_option_id)
    console.log(charger_id)

    console.log("option_name & charger_name")
    console.log(option_name)
    console.log(charger_name)

    //battery_idおよびcharger_idそれぞれのnameを持つ配列を作成
    interface name {
        this_name: string,
        this_idx: number,
    }
    let batterys_name :name [] = []
    let chargers_name :name [] = []

    if (battery_option_id.length>0){
        for (let i=0;i<battery_option_id.length;i++){
            //let tmp :name = {this_name:"車体"+String(battery_option_id[i]),this_idx:i} 
            let tmp :name = {this_name:option_name[i],this_idx:i}
            batterys_name.push(tmp) 
        }
    }
    if(charger_id.length>0){
        for (let i=0;i<charger_id.length;i++){
            //let tmp :name = {this_name:"充電器"+String(charger_id[i]),this_idx:i}//要検討
            let tmp :name = {this_name:charger_name[i],this_idx:i}
            chargers_name.push(tmp)
        }
    }

    console.log("batterys & chargers_name")
    console.log(batterys_name)
    console.log(chargers_name)

    return (
        <Center flexDirection="column">
            <Text
                w="320px"
                fontSize="18px"
                fontWeight="bold"
                margin="20px"
            >
                バッテリーおよび充電器の製造オーダー作成
            </Text>
            <Flex direction="row" marginBottom="5px">
                <Text
                    w="80px"
                    fontSize="16px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    企業名
                </Text>
                <Text
                    w="150px"
                    fontSize="16px"
                    fontWeight="bold"
                >
                    {company}
                </Text>
            </Flex>
            <Flex direction="row" marginBottom="5px">
                <Text
                    w="80px"
                    fontSize="16px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    事業所
                </Text>
                <Text
                    w="150px"
                    fontSize="16px"
                    fontWeight="bold"
                >
                    {officeName}
                </Text>
            </Flex>
            <Flex direction="row" marginBottom="5px">
                <Text
                    w="80px"
                    fontSize="16px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    契約名
                </Text>
                <Text
                    w="150px"
                    fontSize="16px"
                    fontWeight="bold"
                >
                    {contractName}
                </Text>
            </Flex>
            <Flex direction="row" marginBottom="30px">
                <Text
                    w="80px"
                    fontSize="16px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    契約ID
                </Text>
                <Text
                    w="150px"
                    fontSize="16px"
                    fontWeight="bold"
                >
                    {contract_id}
                </Text>
            </Flex>
            <Box marginBottom="10px">
                <AddButton
                    label="バッテリーを載せる車体を選ぶ"
                    onClick={() => {
                        history.push({
                            pathname: `/car-detail`,
                            state: {
                                company: company,
                                officeName: officeName,
                                contractName: contractName,
                                contract_id: contract_id,
                                account_id: account_id,
                                department_id: department_id,
                                battery_option_id: battery_option_id,
                                charger_id: charger_id,
                                charger_name: charger_name,
                                option_name: option_name,
                                //carName: carNameState,
                                //chargerName: chargerNameState,
                            }
                        });
                    }}
                /> 
            </Box>
            {batterys_name.map((value) => {
                    console.log(value.this_idx)
                    cnt++;
                    return (   
                        //battery optionから選ばれた車体を表示 
                        //carNameState &&
                        <Box marginBottom="30px">
                            <CarMiniCard
                                //name={carNameState}
                                name = {value.this_name}
                                onRemoveHandler={() => {
                                    //setCarNameState("");
                                    option_name.splice(value.this_idx,1)
                                    battery_option_id.splice(value.this_idx,1)
                                    console.log(battery_option_id)
                                    setUpdate(cnt);
                                }}
                            />
                        </Box>
                    );
            })}
            <Box marginBottom="10px">
                <AddButton
                    label="製造する充電器を選ぶ"
                    onClick={() => {
                        history.push({
                            pathname: `/charger-detail`,
                            state: {
                                company: company,
                                officeName: officeName,
                                contractName: contractName,
                                contract_id: contract_id,
                                account_id: account_id,
                                department_id: department_id,
                                battery_option_id: battery_option_id,
                                charger_id: charger_id,
                                charger_name: charger_name,
                                option_name: option_name,
                                //carName: carNameState,
                                //chargerName: chargerNameState,
                            }
                        });
                    }}
                /> 
            </Box>
            {chargers_name.map((value) => {
                    cnt++
                    return ( 
                        /*
                        //{
                            //chargerNameState &&
                            value &&
                            <Box marginBottom="30px">
                                <ChargerMiniCard
                                    name={chargerNameState}
                                    onRemoveHandler={() => {
                                        setChargerNameState("");
                                    }}
                                />
                            </Box>
                        //}
                        */
                        <Box marginBottom="30px">
                            <ChargerMiniCard
                                name= {value.this_name}
                                onRemoveHandler={() => {
                                    //setChargerNameState("");
                                    charger_name.splice(value.this_idx,1)
                                    charger_id.splice(value.this_idx,1)
                                    console.log(charger_id)
                                    setUpdate(cnt);
                                }}
                            />
                        </Box>
                    );
            })}
            <Button
                w="260px"
                color="#504E4E"
                bgColor="white"
                border="2px"
                borderColor="#504E4E"
                marginTop="40px"
                onClick={() => {
                    history.push({
                        pathname: `/battery-registration-review`,
                        state: {
                            company: company,
                            officeName: officeName,
                            contractName: contractName,
                            contract_id: contract_id,
                            account_id: account_id,
                            department_id: department_id,
                            battery_option_id: battery_option_id,
                            charger_id: charger_id,
                            charger_name: charger_name,
                            option_name: option_name,
                            //carName: carNameState,
                            //chargerName: chargerNameState,
                        }
                    });
                }}
            >
                登録内容を確認
            </Button>
        </Center>
    );
}

export default BatteryRegistrationViewPage;