import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import {
  Center,
  Box,
  Text,
  Input,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Button,
  Flex,
} from "@chakra-ui/react";
import { ChevronDownIcon } from "@chakra-ui/icons";
import HeaderSubBack from "../../components/HeaderSubBack";

type FormInputs = {
  contractName: string;
  contractStartDate: string;
  contractEndDate: string;
  contractType: string;
};

interface ContractRegistrationViewPageProps {
  location: {
    state: {
      company: string; //corporation name
      officeName: string;
      account_id: number;
      department_id: number;
      is_update: boolean;
      contractData?: {
        contractName: string;
        contractStartDate: string;
        contractEndDate: string;
        contractType: string;
      };
    };
  };
}

const ContractRegistrationViewPage = (
  props: ContractRegistrationViewPageProps
) => {
  const { location } = props;
  const company = location.state.company;
  const officeName = location.state.officeName;
  const account_id = location.state.account_id;
  const department_id = location.state.department_id;
  const is_update= location.state.is_update;
  //console.log(department_id)
  const [contractTypeInputState, setContractTypeInputState] = useState("");

  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm<FormInputs>({
    defaultValues: {
      contractName: location.state.contractData?.contractName || "",
      contractStartDate: location.state.contractData?.contractStartDate || "",
      contractEndDate: location.state.contractData?.contractEndDate || "",
      contractType: location.state.contractData?.contractType || "",
    },
  });

  const onSubmit = (data: FormInputs) => {
    history.push({
      pathname: `/contract-review`,
      state: {
        company: company,
        officeName: officeName,
        account_id: account_id,
        department_id: department_id,
        contractData: {
          contractName: data.contractName,
          contractStartDate: data.contractStartDate,
          contractEndDate: data.contractEndDate,
          contractType: data.contractType,
        },
      },
    });
  };

  const history = useHistory();

  return (
    <div>
        <HeaderSubBack
          headerText="新規契約の登録"
          onClick={() => {
            history.push({
              pathname: "/department-registration",
              state: {
                company: company,
                officeName: officeName,
                account_id: account_id,
                department_id: department_id,
                is_update: is_update,
            },
            });
          }}
        />
    <Center padding="15px" flexDirection="column">
      <form onSubmit={handleSubmit(onSubmit)}>
        <Box margin="20px">
          <Text w="260px" fontSize="22px" fontWeight="bold">
            新規契約登録
          </Text>
        </Box>
        <Box margin="20px">
          <Box marginBottom="15px">
            <Text
              w="260px"
              fontSize="16px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              企業名
            </Text>
            <Text w="260px" fontSize="16px" fontWeight="bold" marginBottom="15px">
              {company}
            </Text>
            <Text
              w="260px"
              fontSize="16px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              事業所名
            </Text>
            <Text w="260px" fontSize="16px" fontWeight="bold" marginBottom="20px">
              {officeName}
            </Text>
            </Box>
            <Box marginBottom="15px">
            <Flex
              direction="row"
              //w="260px"
              fontSize="11px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              <Text>契約名</Text>
              {errors.contractName && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            </Box>
            <Input
              w="260px"
              marginBottom="15px"
              placeholder="例) テスト契約"
              isInvalid={errors.contractName ? true : false}
              {...register("contractName", { required: true })}
            />
            <Flex
              direction="row"
              //w="260px"
              fontSize="11px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              <Text>契約開始年月</Text>
              {errors.contractStartDate && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="260px"
              marginBottom="15px"
              placeholder="例) 2021/04/01"
              isInvalid={errors.contractStartDate ? true : false}
              {...register("contractStartDate", { required: true })}
            />
            <Flex
              direction="row"
              //w="260px"
              fontSize="11px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              <Text>契約終了年月</Text>
              {errors.contractEndDate && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="260px"
              marginBottom="15px"
              placeholder="例) 2023/03/31"
              isInvalid={errors.contractEndDate ? true : false}
              {...register("contractEndDate", { required: true })}
            />
            <Flex
              direction="row"
              //w="260px"
              fontSize="11px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              <Text>契約タイプ</Text>
              {errors.contractType && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Menu>
              <MenuButton
                w="260px"
                h="40px"
                transition="all 0.2s"
                borderRadius="md"
                borderWidth="1px"
                justifyContent="space-between"
                //marginBottom="0px"
              >
                <Flex w="260px" justifyContent="space-between">
                  <Text
                    marginLeft="10px"
                    isInvalid={errors.contractType ? true : false}
                    {...register("contractType", { required: true })}
                  >
                    {contractTypeInputState === ""
                      ? "選択してください"
                      : contractTypeInputState}
                  </Text>
                  <ChevronDownIcon marginRight="10px" marginTop="4px" />
                </Flex>
              </MenuButton>
              <MenuList>
                <MenuItem
                  onClick={() => {
                    setContractTypeInputState("テスト導入");
                  }}
                >
                  テスト導入
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    setContractTypeInputState("売却契約");
                  }}
                >
                  売却契約
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    setContractTypeInputState("リース契約");
                  }}
                >
                  リース契約
                </MenuItem>
              </MenuList>
            </Menu>
        </Box>
        <Button
          w="260px"
          h="40px"
          margin="20px"
          color="#504E4E"
          bgColor="white"
          border="2px"
          borderColor="#504E4E"
          type="submit"
        >
          登録内容を確認
        </Button>
      </form>
    </Center>
    </div>
  );
};

export default ContractRegistrationViewPage;
