import React from "react";
import { useHistory } from 'react-router-dom';
import {Center, Box, Divider, Text, Avatar, Image} from "@chakra-ui/react";
import axios from 'axios';
import {apiURL,BatteryOption,BatteryOptionJson,Contracts,Charger,ChargerJson,Departments,Units,BatteryRequest} from "../../modules/interfaces"

import BatteryCard from "../../components/cards/BatteryCard";
import CarCard from "../../components/cards/CarCard";
import ChargerCard from "../../components/cards/ChargerCard";
import CarOrderCard from "../../components/cards/CarOrderCard";

import BatteryErrorCard from "../../components/cards/BatteryErrorCard";
import BatteryProductionCard from "../../components/cards/BatteryProductionCard";
import BatteryUndefinedCard from "../../components/cards/BatteryUndefinedCard";

import Header from "../../components/Header";
import AddButton from "../../components/buttons/AddButton";
import AddCustomerMenu from "../../components/action-menu/AddContractMenu";
import AddContractMenu from "../../components/action-menu/AddCustomerMenu";
import SearchMenu from "../../components/action-menu/SearchMenu";
import { getValueTransition } from "framer-motion/types/animation/utils/transitions";

const HomePage = () => {

    interface notion{
        massage: string,
        massage2: string,
        color: string,
    }

    //battery requestの取得
    const defaultRequests:BatteryRequest[] = [];
    const [requests, setRequests]: [BatteryRequest[], (requests: BatteryRequest[]) => void] = React.useState(defaultRequests);
    React.useEffect(() => {
        axios
            .get<BatteryRequest[]>(apiURL+"api/v1/batteryrequests/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setRequests(response.data);
            });
        }, []);
        console.log('battery requests data should be fetched at this point.')

    //充電器データの取得
    //charger optionデータの取得
    const defaultChargers:Charger[] = [];
    const [chargers, setChargers]: [Charger[], (chargers: Charger[]) => void] = React.useState(defaultChargers);

    React.useEffect(() => {
        axios
            .get<Charger[]>(apiURL+"api/v1/chargers/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setChargers(response.data);
            });
        }, []);
        console.log('chargers data should be fetched at this point.');
        //console.log(chargers)

    //info_typeがfieldかつcontract_idが0「以外」のもののみを取得
    let fieldChargers:Charger[] = [];
    if(chargers!=null && chargers.length!=0){
        for (let i=0;i<chargers.length;i++){
            if(chargers[i].info_type=="field" && chargers[i].contract_id.Int32!=0){
                fieldChargers[i]=chargers[i]
            }
        }
    }

    let notion:notion[]=[]
    let cnt = 0
    if (requests.length>0 || fieldChargers.length>0){
        cnt++
    }

    if (cnt>0){
        notion.push({massage:"製造リクエスト未作成の契約",massage2:"作成された契約の中で、製造リクエストが未作成のものがあります。\n リクエスト作成を完了させて、工場での製造を開始できるようにしてください",color:"black"})
    }else{
        notion.push({massage:"",massage2:"",color:"black"})
    }

    //バッテリー一斉取得
    const defaultUnits:Units[] = [];
    const [units, setUnits]: [Units[], (units: Units[]) => void] = React.useState(defaultUnits);

    //->battery_id取得
    React.useEffect(() => {
    axios
        .get<Units[]>(apiURL+"api/v1/units/", {
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            setUnits(response.data);
        });
    }, []);
    console.log('units data should be fetched at this point');
    //console.log(units)
    
    let error_units:Units[]=[]
    let notion2:notion[]=[]
    let cnt2 = 0

    if (units.length!=0){
        for(let i=0;i<units.length;i++){
            if(units[i].required_action!=""){
                error_units[i]=units[i]
                cnt2++
            }
        }
    }
    if (cnt2>0){
        notion2.push({massage:"終了日の近い契約",massage2:"以下の契約で終了日が近づいています",color:"black"})
    }else{
        notion2.push({massage:"",massage2:"",color:"black"})
    }

    const history = useHistory();

    //errorがあるかどうかchk
    const iconname = "default"

    return(
        <div>
        <Header
          drawerItems={[
            {
              title: "ホーム",
              path: "/",
            },
            {
              title: "バッテリー",
              path: "/units",
            },
            {
              title: "お客様",
              path: "/customer-view",
            },
          ]}
          //iconName={iconname}
        />
        <Center flexDirection="column">
            <Box>
                <Image
                    //src={"data:image/png;base64,"}
                />
                {/*
                <BatteryErrorCard onClick={()=>{
                    //TODO:遷移の実装
                    history.push(`/units`,);
                }}/>
                <BatteryUndefinedCard onClick={()=>{
                    //TODO:遷移の実装
                }}/>
                */}
            </Box>
            <Box margin="15px">
                <Box marginTop="10px">
                    <AddButton
                        label="新しい契約を作成"
                        onClick={() => {
                            history.push(`/contract-home`);
                        }}
                    />
                    {/*
                    <AddContractMenu onClick={()=>{
                        history.push(`/contract-home`);
                    }}/>
                    */}
                </Box>
                {/*
                <Box marginTop="10px">
                    <AddCustomerMenu onClick={()=>{
                        history.push(`/customer-view`);
                    }}/>
                </Box>
                */}
                {/*
                <Box marginTop="10px">
                    <SearchMenu onSearchHandler={()=>{
                        //TODO:検索して遷移の実装
                    }}/>
                </Box>
                */}
            </Box>
            {/*<Divider/>*/}
            {notion.map((value) => {
                return(
                    <Box>
                    <Text fontWeight="bold" padding="15px" marginTop="10px" marginBottom="0px" marginLeft="30px" marginRight="30px" color={value.color} fontSize="16px">
                        {value.massage}
                    </Text>
                    <Text marginTop="-20px" padding="15px" marginBottom="0px" marginLeft="30px" marginRight="30px" color={value.color} fontSize="10px">
                        {value.massage2}
                    </Text>
                    </Box>
                );
            })}

            {requests.map((value) => {
                return(
                    <Box margin = "5px">
                        {/*
                        <CarCard
                            name={name} //”車体"+string(battery_option_id)
                            car={value.option_name}
                            carType={value.forklift}
                            productNum={value.serial_number}
                        />
                        */}
                        <CarOrderCard
                            name={value.option_name} //”車体"+string(battery_option_id)
                            //option_name={value.option_name}
                            department_name={value.department_name}
                            contract_name={value.contract_name}
                        />
                    </Box>
                );
            })}
            
            
            {fieldChargers.map((value) => {
                let id = value.charger_id
                //let name = "車体"+String(id)
                let name = value.charger_name
                let frame = false
                if (value.stand=="need"){
                    frame = true
                }
                return(
                    <Box margin = "5px">
                        <ChargerCard
                            name={name}
                            provideMethod={value.how2supply}
                            powerAmp={String(value.power_supply_ampere.Int32)}
                            isNeedFrame={frame}
                        />
                    </Box>
                );
            })}
            <Box margin="10px"></Box>
            <Divider/>
            {notion2.map((value) => {
                return(
                    <Box>
                    <Text fontWeight="bold" padding="15px" marginTop="10px" marginBottom="0px" marginLeft="30px" marginRight="30px" color={value.color} fontSize="16px">
                        {value.massage}
                    </Text>
                    <Text marginTop="-20px" padding="15px" marginBottom="0px" marginLeft="30px" marginRight="30px" color={value.color} fontSize="10px">
                        {value.massage2}
                    </Text>
                    </Box>
                );
            })}
            
            <Box margin="15px">
                {error_units.map((value) => {
                    return (
                        <Box marginTop="15px">
                            <BatteryCard
                                remainingAmount={value.soc}
                                unitID={value.unit_id}
                                batteryType={value.profile.unit_type}
                                company={value.customer_name}
                                purpose={value.profile.purpose}
                                requiredAction={value.required_action}
                                onClick={()=>{
                                    history.push({
                                        pathname: `/unit-detail`,
                                        state: {
                                            contractID: value.contract_id,
                                            unitID: value.unit_id
                                        }
                                    });
                                }}
                            />
                        </Box>
                    );
                })}
            </Box>
        </Center>
        </div>
    );
};

export default HomePage;