import React from 'react';
import {
    Flex,
    Text,
    Center,
    Image
} from "@chakra-ui/react";
import rocket_icon from "../../assets/rocket_icon.png";

interface SubmitContractCardProps {
    iconURL?: string,
    companyName: string,
    officeName: string,
    contractName: string,
    contractID: string,
    isLackData?: boolean,
    onClick?: () => void;
}

const SubmitContractCard = (props: SubmitContractCardProps) => {
    const {
        iconURL,
        companyName,
        officeName,
        contractName,
        contractID,
        isLackData,
        onClick,
    } = props;

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="column"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick()
                }
            }}
        >
            <Flex direction="row">
                <Image w="63px" src={iconURL || rocket_icon}/>
                <Flex direction="column" marginLeft="20px">
                    <Text
                        fontSize="14px"
                        fontWeight="bold"
                        color="#504E4E"
                    >
                        {companyName}
                    </Text>
                    <Flex>
                        <Text
                            fontSize="14px"
                            fontWeight="bold"
                            color="#919191"
                            w="50px"
                            >
                                事業所
                            </Text>
                        <Text
                            fontSize="14px"
                            fontWeight="bold"
                            color="#504E4E"
                            w="100px"
                        >
                            {officeName}
                        </Text>
                    </Flex>
                    <Flex>
                        <Text
                            fontSize="14px"
                            fontWeight="bold"
                            color="#919191"
                            w="50px"
                            >
                                契約名
                            </Text>
                        <Text
                            fontSize="14px"
                            fontWeight="bold"
                            color="#504E4E"
                            w="100px"
                        >
                            {contractName}
                        </Text>
                    </Flex>
                    <Flex>
                        <Text
                            fontSize="14px"
                            fontWeight="bold"
                            color="#919191"
                            w="50px"
                            >
                                契約ID
                            </Text>
                        <Text
                            fontSize="14px"
                            fontWeight="bold"
                            color="#504E4E"
                            w="100px"
                        >
                            {contractID}
                        </Text>
                    </Flex>
                </Flex>
            </Flex>
            {
                isLackData &&
                <Text
                    fontSize="14px"
                    fontWeight="bold"
                    color="#BB2C2C"
                >
                    製造に必要な情報が不足しています
                </Text>
            }
        </Center>
    );
}

export default SubmitContractCard;