import React from 'react';
import { useHistory } from 'react-router-dom';
import {
    Center,
    Text,
    Button,
    Box,
} from '@chakra-ui/react';

import ContractCompleteCard from "../../components/cards/ContractCompleteCard";
import StatusBar from "../../components/StatusBar"

interface ContractRegistrationViewPageProps {
    location: {
        state:{
            company: string,
            officeName: string,
            account_id: number,
            department_id: number,
            contractName: string,
            contract_id: number,
        }
    }
}

const ContractRegistrationViewPage = (props: ContractRegistrationViewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const contractName = location.state.contractName;
    const contract_id = location.state.contract_id;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;

    const history = useHistory();

    return (
        <Center flexDirection="column">
            <Text
                fontSize="22px"
                fontWeight="bold"
                marginTop="50px"
            >
                契約が新たに登録されました
            </Text>
            <Box margin="30px">
                <ContractCompleteCard
                    companyName={company}
                    contractName={contractName}
                    contractID={String(contract_id)}
                />
            </Box>
            <Box
                w="260px"
                color="#504E4E"
                fontSize="14px"
                fontWeight="bold"
            >
                <Text>
                    次に
                </Text>
                <Text>
                    バッテリーを載せる車体情報および充電情報を選択し、製造オーダーを作成してください
                </Text>
            </Box>
            <StatusBar activeStep={1}/>
            <Button
                w="260px"
                color="#504E4E"
                bgColor="white"
                border="2px"
                borderColor="#504E4E"
                marginTop="80px"
                onClick={() => {
                    history.push({
                        pathname: `/battery-registration`,
                        state: {
                            company: company,
                            officeName: officeName,
                            contractName: contractName,
                            contract_id: contract_id,
                            account_id: account_id,
                            department_id: department_id,
                        }
                    });
                }}
            >
                次へ
            </Button>
        </Center>
    );
}

export default ContractRegistrationViewPage;